        </div>
        <div class="sms-linkman-content">
            <!--  请打开读取联系人权限 -->
            <div v-if='linkmanModal'>
                <GrayMask
                    :show="linkmanModal">
                    <div class="linkman-modal">
                        <div
                            @click='closeLinkmanModal'
                            class="linkman-modal-close"/>
                        <div class='linkman-modal-title'>请打开读取联系人权限</div>
                        <div class='linkman-modal-content'/>
                        <div
                            class='linkman-modal-footer'
                            @click="getContactList"
                        >开启权限+500金币</div>
                        <p class="linkman-modal-footer-declare">
