                            <span @click="openPrivacyWebview">隐私政策</span>
                        </p>
                    </div>
                </GrayMask>
            </div>
            <div
                v-else-if="!linkmanModal && !linkmanAuth"
                class="refuse-linkman">
                <!-- 返回关闭弹框引导开启权限 -->
                <div class="refuse-linkman-banner"/>
                <div class="refuse-linkman-content">
                    <p class="title">发现你潜力巨大</p>
                    <p>邀请通讯录好友可得高额现金</p>
                </div>
                <div
                    @click='refuseLinkManBtn'
                    class="refuse-linkman-btn">
                    识别好友领现金
                </div>
                <p class="linkman-modal-footer-declare">
                    开启即代表您已阅读并同意
                    <span @click="openPrivacyWebview">隐私政策</span>
                </p>
                <div class="linkman-watermark"/>
            </div>
            <Smslinkman
                @batchInvite = 'batchInvite'
                @openSuccessSendMsg='openSuccessSendMsg'
                v-else-if='!linkmanRequing && linkmanAuth'
                :linkman='linkmanData'/>
            <div v-if='linkmanRequing && linkmanAuth'>
                <!-- 正在获取联系人数据 -->
                <div class="loading">
                    正在获取联系人请稍后....
                </div>
                <div class="linkman-watermark"/>
            </div>
        </div>
        <GrayMask
            :show="successSendMsg">
            <div class="sendMsg-modal">
                <div
                    @click="closeSendMsgModal"
                    class="sendMsg-modal-close"/>
                <div class='sendMsg-modal-title'>
                    <p class="sendMsg-banner">短信发送成功</p>
                    <p>快去告诉好友如何赚红包吧！</p>
                </div>
                <div class='sendMsg-modal-content'>
                    <div
                        :key='index'
                        class="sndeMsg-success-text"
                        v-for="(item,index) in smsSuccessText">
                        <div class="sndeMsg-success-text-serial">
                            {{ index+1 }}
                        </div>
                        <span
                            class="success-text"
                            v-html="item"/>
                    </div>
                </div>
                <div
