                <div class='linkman-modal-title'>请升级到最新APP版本</div>
                <div class="update-modal-content"/>
                <div
                    @click="updateVersion"
                    class="update-modal-btn">
                    立即升级
                </div>
            </div>
        </GrayMask>
    </div>
