<script>
import NavBar from '@/components/NavBar';
import GrayMask from '@/components/GrayMask';
import { isXiangkan } from '@/utils/ua';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import config from '@/config';
// 客户端方法
import {
    goSetting,
    openCommonWebView,
    isTakeOverBackByWeb,
    doLogin,
    getUserAccountInfo,
    toast } from '@/utils/jsApiV2/client';
import {
    checkContactsPermission,
    checkPermissionAndGetContactList,
    getContactsInfo,
    sendMessage } from '@/utils/jsApiV2/act';
// 服务端接口
import { smsUpload, smsStatus, smsInvited, smsHome } from '@/api/invitation';
import { getShareUrl, shareCodeTextToWechat } from '@/utils/share';
import { smsSuccessText } from './config';
import Smslinkman from './components/sms-linkman';

const clientCheckContactsPermission = 'clientCheckContactsPermission';
const clientCheckPermissionAndGetContactList = 'clientCheckPermissionAndGetContactList';
const clientGetContactsInfo = 'clientGetContactsInfo';
let keys = [];
let statusList = [];
let pollingTimers = 0;


export default {
    name: 'SmsInvite',
    components: {
        NavBar,
        GrayMask,
        Smslinkman,
    },
    data() {
        return {
            userInfo: {},
            needUpdate: config.versionCode < 40780,
            linkmanModal: false,
            linkmanAuth: false,
            linkmanRequing: true,
            prevLinkManDara: [],
            linkmanData: [],
            successSendMsg: false,
            smsSuccessText,
            invitationCode: '',
            smsTpl: '',
            url: '',
            updateVersionModal: false,
            acceptTimers: 0,
        };
    },
    created() {
        this.$root.hidePlaceholder();
        // 检查客户端版本
        if (this.needUpdate) {
            this.updateVersionModal = true;
            return;
        }
        // 检查用户是否登录并获取相关数据
        this.userInfo = getUserAccountInfo();
        this.getCodeAndShareUrl();
        window.clientCheckContactsPermission = this.clientCheckContactsPermission;
        window.clientCheckPermissionAndGetContactList = this.clientCheckPermissionAndGetContactList;
        window.clientGetContactsInfo = this.clientGetContactsInfo;
        // 接管原生back键操作
        isTakeOverBackByWeb(true);
        addCustomEventListener('backKey', this.backKeyHandler);
    },
    destroyed() {
        // 释放原生事件
        isTakeOverBackByWeb(false);
        removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        getCodeAndShareUrl() {
            smsHome().then((res) => {
                this.invitationCode = res.invitationCode;
                this.url = `${window.location.origin}/mobile-v2/act/invitation/smspage?shareId=${this.invitationCode}`;
                if (!this.invitationCode) {
                    doLogin('location.reload');
                    return;
                }
                // 检查读取手机联系人权限
                checkContactsPermission(clientCheckContactsPermission);
                getShareUrl(this.url).then((ret) => {
                    this.smsTpl = res.smsTpl.replace(/url/, ret);
                }).catch(() => {
                    this.$error();
                });
            }).catch(() => {
                this.$error();
            });
        },
        backKeyHandler() {
            this.$router.push('/act/invitation');
        },
        clickBackHandler() {
            this.$router.push('/act/invitation');
        },
        openPrivacyWebview() {
            openCommonWebView('http://www.xiangkannews.cn/privacy');
        },
        refuseLinkManBtn() {
            this.$o2o({
                e_c: '短信邀请',
                e_a: '点击',
                e_n: '识别好友领现金点击',
            });
            this.getContactList();
        },
        clientCheckContactsPermission(value) {
            if (value) {
                this.getContactList();
                return;
            }
            this.linkmanModal = !value;
            this.$o2o({
                e_c: '短信邀请',
                e_a: '曝光',
                e_n: '打开权限弹窗曝光',
            });
        },
        getContactList() {
            if (!this.invitationCode) {
                doLogin('location.reload');
                return;
            }
            checkPermissionAndGetContactList(clientCheckPermissionAndGetContactList);
        },
        clientCheckPermissionAndGetContactList(result) {
            if (!result.accept) {
                this.acceptTimers += 1;
                if (this.acceptTimers % 3 === 0) {
                    toast('请前往设置页面开启联系人访问权限');
                }
                return;
            }
            this.linkmanModal = false;
            this.linkmanAuth = true;
            this.$o2o({
                e_c: '短信邀请',
                e_a: '点击',
                e_n: '打开权限弹窗点击',
            });
            // 通讯录有变化
            if (result.isChanged) {
                // 向后端上传通讯录
                smsUpload({ list: JSON.stringify(result.data) }).then(() => {
                    this.linkmanRequing = true;
                    this.getSmsStatus();
                }).catch(() => {
                    this.$error();
                });
            } else {
                this.linkmanRequing = true;
                this.getSmsStatus();
            }
        },
        getSmsStatus() {
            // 获取加密通讯录信息
            smsStatus().then((res) => {
                if (res.isFinish || pollingTimers >= 10) {
                    this.linkmanRequing = false;
                    this.handleLinkMan(res.contact || []);
                } else {
                    pollingTimers += 1;
                    this.getSmsStatus();
                }
            }).catch(() => {
                this.$error();
            });
        },
        openSuccessSendMsg() {
            this.successSendMsg = true;
        },
        handleLinkMan(result) {
            keys = Object.keys(result);
            statusList = result;
            getContactsInfo(JSON.stringify({ data: keys }), clientGetContactsInfo);
        },
        clientGetContactsInfo(res) {
            const resList = res.data;
            // 按照联系人首字母进行排序  按照是否邀请中进行排序
            // eslint-disable-next-line max-len
            this.linkmanData = resList.map(item => ({ status: statusList[item.phoneEncrypt], ...item })).sort((a, b) => b.status - a.status);
        },
        closeLinkmanModal() {
            this.linkmanModal = false;
            this.$o2o({
                e_c: '短信邀请',
                e_a: '曝光',
                e_n: '短信邀请主页曝光',
                e_x: { Page: '无权限' },
            });
        },
        closeSendMsgModal() {
            this.successSendMsg = false;
        },
        updateVersion() {
            goSetting();
        },
        isShowNavbar() {
            return isXiangkan();
        },
        batchInvite(list) {
            const phoneMd5Str = list.map(item => item.phoneEncrypt);
            this.linkmanData = this.linkmanData.map((item) => {
                // eslint-disable-next-line no-param-reassign
                item.status = this.seekExistInList(item, list) ? 1 : item.status;
                return item;
            });
            sendMessage(JSON.stringify({ data: phoneMd5Str }), this.smsTpl);
            this.$nextTick(() => {
                smsInvited({ list: JSON.stringify(phoneMd5Str) }).then(() => {
                    // 短信发送成功
                    this.successSendMsg = true;
                    this.$o2o({
                        e_c: '短信邀请',
                        e_a: '曝光',
                        e_n: '短信发送成功弹框',
                    });
                }).catch(() => {
                    this.$error();
                });
            });
        },
        seekExistInList(target, list) {
            return list.find(item => item.phoneEncrypt === target.phoneEncrypt);
        },
        webchatRemind() {
            this.successSendMsg = false;
            shareCodeTextToWechat(this.url, this.invitationCode);
            this.$o2o({
                e_c: '短信邀请',
                e_a: '点击',
                e_n: '微信提醒',
            });
        },
    },
};
</script>

