<template>
    <div>
        <div
            class="sms-link-nolinkman"
            v-if='!linkmanData.length'>
            <p>通讯录暂无可用联系人</p>
            <!-- 无可用联系人 都是想看用户了 -->
        </div>
        <div
            v-else
            class="sms-linkman-content">
            <div
                class="linkman"
                @click="choose(index)"
                :key='index'
                v-for="(item,index) in linkmanData">
                <div
                    :class='[
                        item.checked?"checked":"uncheck",
                        item.status===invited?"disabled-check":"",
                        "checkbox"]' />
                <div class="left">
                    <div class="linkman-name">
                        {{ limitLength(item.name,6) }}
                    </div>
                    <div class="linkman-phone">
                        {{ item.phone }}
                    </div>
                </div>
                <div
                    @click.stop="select(item)"
                    :class='[item.status=== uninvite?"inviting":"uninvite","linkman-status"]'>
                    {{ computeStatus(item.status) }}
                </div>
            </div>
        </div>
        <div class="sms-linkman-footer">
            <div class="linkman-footer-left">
                <div
                    @click="selectAll"
                    :class='[checkAll?"checked":"uncheck","check-all"]'/>
                全选
            </div>
            <div class="linkman-footer-right">
                预计得到
                <span style='color: #fa4137;'>￥</span>
                <div
                    @click="batchInviteClick"
                    :class='[
                        // eslint-disable-next-line max-len
                        selectData.length ? "linkman-footer-select-btn":"linkman-footer-unselect-btn",
                        "linkman-footer-btn"]'
                >
                    批量邀请
                </div>
                <span class="linkman-footer-price">{{ price }}</span>
            </div>
        </div>
    </div>
</template>

<script>
import { toast } from '@/utils/jsApiV2/client';


export default {
    name: 'Smslinkman',
    props: {
        linkman: {
            type: Array,
            default() {
                return [];
            },
        },
    },
    data() {
        return {
            linkmanData: [],
            checkAll: false,
            invited: 1,
            uninvite: 0,
        };
    },
    created() {
        this.$o2o({
            e_c: '短信邀请',
            e_a: '曝光',
            e_n: '短信邀请主页曝光',
            e_x: { Page: '有权限' },
        });
    },
    computed: {
        price() {
            let number = 0;
            // eslint-disable-next-line array-callback-return
            this.linkmanData.map((item) => {
                // eslint-disable-next-line no-plusplus
                number = item.checked ? ++number : number;
                return item;
            });
            return number * 4;
        },
        selectData() {
            return this.linkmanData.filter(item => item.checked);
        },
    },
    watch: {
        linkman(v) {
            this.linkmanData = v.map(item => ({
                ...item,
                checked: false,
            }));
        },
    },
    methods: {
        limitLength(str, length) {
            return str.length > length ? `${str.substr(0, length)}...` : str;
        },
        computeStatus(status) {
            return status === this.invited ? '邀请中...' : '邀请+4元';
        },
        choose(index) {
            const newValue = this.linkmanData[index];
            if (newValue.status === this.uninvite) {
                // 未邀请过的可以操作 邀请中的联系人不可操作
                newValue.checked = !newValue.checked;
                // eslint-disable-next-line max-len
                const leftItem = this.linkmanData.find(item => item.status === this.uninvite && item.checked === false);
                this.checkAll = !leftItem;
                this.$set(this.linkmanData, index, newValue);
            }
        },
        selectAll() {
            this.checkAll = !this.checkAll;
            this.linkmanData = this.linkmanData.map((item) => {
                // eslint-disable-next-line no-param-reassign
                item.checked = item.status === this.invited ? item.checked : this.checkAll;
                return item;
            });
        },
        batchInviteClick() {
            if (this.selectData.length) {
                this.$o2o({
                    e_c: '短信邀请',
                    e_a: '点击',
                    e_n: '批量邀请',
                });
                this.$emit('batchInvite', this.selectData);
                return;
            }
            toast('请先勾选当前联系人哦！');
        },
        select(item) {
            if (item.status === this.invited) {
                this.$emit('openSuccessSendMsg');
                return;
            }
            this.$o2o({
                e_c: '短信邀请',
                e_a: '点击',
                e_n: '邀请加8元',
            });
            this.$emit('batchInvite', [item]);
        },
    },
};
</script>

